.swiper_container {
  position: relative;
}

.swiper {
  width: 100%;
  height: 100%;
}

/* .swiper-wrapper{
  margin-bottom: 3rem;
} */
.swiper-slide {
  background: #fff;

}

.category_container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
}

.image_container {
  width: 100%;

}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.category_image {
  width: 100%;
  object-fit: contain;
  height: 220px;
}

.section_title {
  display: flex;
  gap: 100px;
}