.category_item_wrapper {
    width: 100%;
    text-align: center;
    cursor: pointer;
}

.image_wrapper {
    height: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.image_wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.category_title {
    font-size: 18px;
    font-weight: 600;
    margin-top: 20px;
}